@import url(https://fonts.googleapis.com/css2?family=M+PLUS+Rounded+1c&display=swap);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
*{
  margin:0;
  font-family: 'M Plus Rounded 1c'
}
html{
  background-color: #e4e4f8;  
}
.header{
  height: 50px;
  width: 100%;
  background-color: #e4e4f8;
  box-shadow: 0 0  10px rgb(103, 103, 117);
  padding: 5px;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.circleButton{
  height:30px;
  width: 30px;
  border-radius: 50%;
  background-color: #dadaf7;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 0;
}
.circleButton:hover{
  background-color: #ccccfa;  
}
.floatingMenu{
  height: 200px;
  width: 200px;
  border-radius: 5px;
  position: absolute;
  background-color: #e4e4f8;
  box-shadow: 0 0  10px rgb(103, 103, 117);
  top:20px;
  left:0px;
  visibility: hidden;
  overflow: hidden;
  height: 0;
  transition: 500ms;
  z-index: 2;
}
.circleButton:hover > .floatingMenu{
  visibility: visible;
  height: 200px;  
}
.titleMenu{
  position: absolute;
  width: 80px;
  top:-3px;  
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2px;
  box-sizing: border-box;
}
.circleButton:hover > .titleMenu{
  height: 35px;    
}
.menuItem{
  height: 40px;
  border-bottom: 1px solid gray;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  padding-right: 10px;
  padding-left: 10px;
  box-sizing: border-box;
}
.menuItem:hover{
  background-color: #f0f0fa;
}
.sideMenu{
  height: 700px;
  top:50px;
  left:-180px;
  visibility: visible;
  transition: 500ms;
  overflow-y: scroll;
  direction: rtl;
}
.circleButton:hover > .sideMenu{
  left: -5px;
  height: 700px;
}
.rightMenu{
  left: auto;
  right: 15px;
  height: 0;
  transition: 500ms;
}
.circleButton:hover .rightMenu{
  height: 200px;
}
.closeButton{
  height: 20px;
  width: 20px;
  background-color: rgb(241, 153, 153);
  color: white;
  border-radius: 3px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  position: absolute;
  right: 10px;
  padding-bottom:3px;
  box-sizing: border-box;
}
.closeButton:hover{
  background-color: rgb(248, 130, 130);
}
.noteDisplay{
  height: 80vh;
  overflow: scroll;
}
.noteEditContainer{
  width: 100%;
  z-index: 2;
  text-align: center;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 120px;
  left: 0;
  top:0;
}
.noteEdit{
  height: 650px;
  width: 500px;
  border-radius: 5px;
  
  background-color: #e4e4f8;
  box-shadow: 0 0 400px rgb(11, 11, 255);
  padding: 10px;  
  box-sizing: border-box;
  z-index: 2;
}
.noteEdit textarea{
  height:510px;
  width: 95%;
  overflow-y: scroll;
  outline: none;
  border: none;
  background-color: #ececf3;
  border-radius: 5px;
}
.noteEdit input{
  height: 40px;
  width: 90%;
  margin-left: -5%;  
  overflow-y: scroll;
  outline: none;
  border: none;
  background-color: #ececf3;
  margin-bottom: 10px;
  border-radius: 5px;
}
button{
  height: 50px;
  width: 120px;
  border-radius: 5px;
  background-image: linear-gradient(rgb(119, 119, 247), rgb(8, 8, 48));
  color:white;
  font-weight: bold;
  margin: 10px;
}
button:hover{
  background-image: linear-gradient(rgb(168, 168, 224), rgb(21, 21, 100)) ;
}
.flowButtonsContainer{
  width: 100%;
  text-align: center;
  display: flex;
  justify-content: center;
  box-sizing: border-box;  
  margin-top: 20px;
}
.flowButtons{
  height: 80px;
  
  border: 1px solid gray;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
  box-sizing: border-box;
  border-radius: 10px;
}
.bottomButtonContainer{
  width: 100%;
  
  display: flex;
  justify-content: space-between;
}
.bottomButtonContainer button{
  width: 220px;
}
.noteLine{
  height: 40px;
  border-bottom: 1px solid gray;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  padding-right: 10px;
  padding-left: 10px;
  box-sizing: border-box;
}
.noteLine:hover{
  background-color: #f0f0fa;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

